
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#base {
  width: 100%;
  position: relative;
}
.content-outter {
  width: 100%;
  height: 100%;
  position: fixed;
}
.content {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 40px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.copyright {
  position: fixed;
  left: 0px;
  bottom: 22px;
  color: #002059;
  opacity: 0.2;
  text-align: center;
  width: calc(100% - 0px);
}
@media screen and (max-width: 1000px) {
.side {
    display: none;
}
.content {
    padding-left: 0;
    width: 100%;
}
.copyright {
    left: 0;
    width: 100%;
}
}

.login-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
  margin-top: 30px;
}
.form {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
          box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Microsoft YaHei", "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #5581fd;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.form button :hover,
.form button :active,
.form button :focus {
  background: #43A047;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #5581fd;
  text-decoration: none;
}
body {
  background: white;
  /* fallback for old browsers */
  background: -o-linear-gradient(right, #76b852, #8DC26F);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.shake_effect {
  -webkit-animation-name: shake;
  animation-name: shake;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
}
@-webkit-keyframes shake {
from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}
10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
}
20%,
  40%,
  60%,
  80% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
}
}
@keyframes shake {
from,
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}
10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translate3d(-10px, 0, 0);
    transform: translate3d(-10px, 0, 0);
}
20%,
  40%,
  60%,
  80% {
    -webkit-transform: translate3d(10px, 0, 0);
    transform: translate3d(10px, 0, 0);
}
}
p.center {
  color: #fff;
  font-family: "Microsoft YaHei";
}

#register {
  background-color: #B5DEF2;
  margin: 140px 0 140px auto;
}
#register .loginBox {
  background-color: #F0F4F6;
  border: 1px solid #BfD6E1;
  border-radius: 5px;
  color: #444;
  font: 14px 'Microsoft YaHei', '微软雅黑';
  margin: 0 auto;
  width: 388px;
}
#register .loginBox .loginBoxCenter {
  border-bottom: 1px solid #DDE0E8;
  padding: 24px;
}
#register .loginBox .loginBoxCenter p {
  margin-bottom: 10px;
  text-align: left;
}
#register .loginBox .loginBoxCenter .loginInput {
  border: 1px solid #D2D9dC;
  border-radius: 2px;
  color: #444;
  font: 12px 'Microsoft YaHei', '微软雅黑';
  padding: 8px 14px;
  margin-bottom: 8px;
  width: 310px;
}
#register .loginBox .loginBoxCenter .loginInput :focus {
  border: 1px solid #B7D4EA;
  -webkit-box-shadow: 0 0 8px #B7D4EA;
          box-shadow: 0 0 8px #B7D4EA;
}
#register .loginBox .loginBoxButtons {
  border-top: 0px solid #FFF;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  line-height: 28px;
  overflow: hidden;
  padding: 20px 24px;
  vertical-align: center;
  filter: alpha(opacity=80);
  -moz-opacity: 0.5;
  opacity: 0.5;
}
#register .loginBox .loginBoxButtons .loginBtn {
  background-image: -moz-linear-gradient(to bottom, blue, #85CFEE);
  border: 1px solid #98CCE7;
  border-radius: 20px;
  -webkit-box-shadow: inset rgba(255, 255, 255, 0.6) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 1px 1px;
          box-shadow: inset rgba(255, 255, 255, 0.6) 0 1px 1px, rgba(0, 0, 0, 0.1) 0 1px 1px;
  color: #444;
  cursor: pointer;
  font: bold 13px Arial;
  padding: 10px 50px;
}
